<template>
  <div class="new">
    <van-nav-bar class="nav" title="需求详情" left-arrow @click-left="goback" />
    <p class="headline">{{ content.entryname }}</p>
    <span class="time">发布时间：{{ content.time }}</span>
    <van-divider dashed />
    <div class="context">
      <p>
        <span class="font-weight-bold">发布高校：</span>{{ content.uploads }}
      </p>
      <p class="mt-8">
        <span class="font-weight-bold">成果负责人：</span>
        <span class="pl-2">{{ content.charge }}</span>
      </p>
      <p class="mt-8">
        <span class="font-weight-bold">负责人职称：</span>
        <span class="pl-2">{{ content.position }}</span>
      </p>
      <p class="mt-8">
        <span class="font-weight-bold">成果简介：</span>
        <br />
        <span class="mt-1">
          {{ content.results }}
        </span>
      </p>
      <div class="mt-8">
        <span class="font-weight-bold" v-if="content.tecNew">技术创新点：</span>
        <p class="mt-1" v-html="content.tecNew">{{ content.tecNew }}</p>
      </div>
      <div class="mt-8">
        <span class="font-weight-bold" v-if="content.future"
          >市场前景及应用：</span
        >
        <p class="mt-1" v-html="content.future">{{ content.future }}</p>
      </div>
      <p class="mt-8">
        <span class="text-left font-weight-bold" v-if="content.maturity"
          >技术成熟度：</span
        >
        <span>
          {{ content.maturity }}
        </span>
      </p>
      <div class="mt-8">
        <span class="font-weight-bold">联系方式：</span><br />
        <div class="mt-1" v-if="content.contacts">
          <span>联系电话：{{ content.contacts }}</span
          ><br />
        </div>
        <div v-if="content.tel">
          <span>联系电话：{{ content.tel }}</span
          ><br />
        </div>
        <span v-if="content.mail">联系邮箱：{{ content.mail }}</span>
      </div>
    </div>
    <van-divider dashed />
    <div class="up">
      <p>
        <span class="lastNext">上一条：</span
        ><a @click="lastAndNext(last.id)" v-if="last">{{ last.entryname }}</a>
      </p>
      <p class="next">
        <span class="lastNext">下一条：</span>
        <a @click="lastAndNext(next.id)" v-if="next">{{ next.entryname }}</a>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'searchDetail',
  props: ['id'],
  watch: {
    $route() {
      this.getDemand()
    }
  },
  data() {
    return {
      content: {},
      filename: '',
      dialog: false,
      last: {},
      next: {}
    }
  },
  methods: {
    goback() {
      this.$router.push('/researchList')
    },
    async getDemand() {
      this.school = this.$route.query.school
      this.year = this.$route.query.year
      this.subject = this.$route.query.subject
      let res = await this.$http.get(
        '/api/home/readSci?id=' +
          this.id +
          '&config=' +
          this.subject +
          '&school=' +
          this.school +
          '&year=' +
          this.year
      )
      if (res.status != 200) return false
      if (res.data.code != 200) return false
      this.content = res.data.data.Sci
      if (this.content.tecNew !== null) {
        this.content.tecNew = this.content.tecNew.replace(
          /img/g,
          'img style = " max-width:calc(100% - 1rem/2); "'
        )
      }
      if (this.content.future !== null) {
        this.content.future = this.content.future.replace(
          /img/g,
          'img style = " max-width:calc(100% - 1rem/2); "'
        )
      }
      if (res.data.data.Scilast) {
        this.last.entryname = res.data.data.Scilast.entryname
        this.last.id = res.data.data.Scilast.id
      }
      if (res.data.data.Scinext) {
        this.next.entryname = res.data.data.Scinext.entryname
        this.next.id = res.data.data.Scinext.id
      }
    },
    lastAndNext(id) {
      this.$router.push({
        path: '/researchList/detail/' + id,
        query: {
          subject: this.subject,
          school: this.school,
          year: this.year
        }
      })
    }
  },
  mounted() {
    this.getDemand()
  }
}
</script>

<style scoped>
.new {
  max-width: 100%;
  overflow: hidden;
}
.nav {
  background-color: #333744;
}
.headline {
  font-size: 1.25rem;
  font-weight: bold;
  margin: 0.5rem;
}
.font-weight-bold {
  font-weight: 600;
}
.time {
  color: #808080;
  margin-top: 0.75rem;
  font-size: 0.8125rem;
  margin-left: 0.5rem;
}
.context {
  margin: 0 0.5rem;
  min-height: 23.125rem;
  line-height: 150%;
}
.lastNext {
  color: #6d6d6d;
}
.next {
  margin-top: 1.25rem;
}
.mt-1 {
  margin-top: 0.25rem;
}
.mt-8 {
  margin-top: 0.8rem;
}
.up {
  margin: 0 0.5rem;
}
</style>
<style>
.van-nav-bar__title {
  color: #fff;
}
.van-nav-bar .van-icon {
  color: #fff;
}
</style>
